<template>
    <div class="users" v-if="data">
        <HeaderTop />
        <div class="userTop">
            <div class="topbar"><router-link to="/">主页</router-link>/ <span class="username">{{ userName }}</span></div>
        </div>
        <div class="usermain">
            <div class="userinfo">
                <div class="userimg"><img :src="data.avatar_url" alt=""></div><span  class="username">{{data.loginname}}</span>
        </div>
        <div class="score">{{data.score}}  积分</div>

        <div class="firsttime">注册时间 {{firstTime}} </div>
        </div>
        <div class="createtopic">
            <div class="createTop">最近创建的话题</div>
            <div class="createmain">
                <ul>
                   
                    <li v-for="item in data.recent_topics" :key="item.id">
                        <router-link :to="`/topic/${item.id}`"><span class="createimg"> <img :src="item.author.avatar_url" alt=""></span>
                       <span class="createtitle">{{item.title}}</span>
                       <span class="createtime">{{item.last_reply_at}}</span></router-link>
                       
                    </li>
                </ul>
            </div>
        </div>
        <div class="createtopic">
            <div class="createTop">最近参与的话题</div>
            <div class="createmain">
                <ul>
                    <li v-for="item in data.recent_replies" :key="item.id">
                        <router-link  :to="`/topic/${item.id}`">
                            <span class="createimg"> <img :src="item.author.avatar_url" alt=""></span>
                       <span class="createtitle">{{item.title}}</span>
                       <span class="createtime"> {{item.last_reply_at}}</span></router-link>
                       
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import HeaderTop from '../components/HeaderTop.vue';

export default {
    components: { HeaderTop },
    props: ["userName"],
    data() {
        return {
            data:[],
            firstTime:null
        };
    },
  async  created() {
    
     const res =  await  this.$http.get(`/user/${this.userName}`)
     this.data = res.data
     console.log(res.data)
     res.data.recent_replies.forEach(ele => {
        ele.last_reply_at = this.$moment(`${ ele.last_reply_at}`, "YYYYMMDD").fromNow();
     });
     res.data.recent_topics.forEach(ele => {
        ele.last_reply_at = this.$moment(`${ ele.last_reply_at}`, "YYYYMMDD").fromNow();
     });
     this.firstTime= this.$moment(`${res.data.create_at}`, "YYYYMMDD").fromNow();
    },

}
</script>

