    width: 1100px;
    margin: 0 auto;
    padding: 10px;
    padding-right: 300px;
    background-color: #fff;
    box-sizing: border-box;
    text-align: left;
}

.userinfo .userimg{
    display: inline-block
}
.userinfo .userimg img{
    width: 40px;
}
.userinfo  .username{
    font-size: 14px;
    color:#778087;
